let demo = document.querySelector('#demo');
let style = document.querySelector('#style');
let string = `
/*你好，打个招呼认识一下,
 *我要做一个太极，接下来我要做一个样式了
 *我要加的样式是*/
 body{
  color:red
}
               `
let string2 = '';

let n = 0;
let step = () => {
    setTimeout(() => {
        n = n + 1;
        if(string[n] ==="\n"){
            string2 = string2 + "<br>";
        }else if(string[n] ===" "){
            string2 = string2 +"&nbsp"
        }else{
            string2 = string2 + string[n];
        }
        if (n+1 < string.length) {
            step();
        }else{

        }
    demo.innerHTML = string2;
    style.innerHTML = string.substring(0,n);
    }, 200);
   
}

step();